<template>
    <div class="page-container">
        <el-aside class="page-nav-left">
            <div class="aside-bar">
                <el-menu default-active="3">
                    <router-link to="/trace/batch">
                        <el-menu-item index="0">
                            <i class="el-icon-position"></i>
                            <span slot="title">溯源管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/certify">
                        <el-menu-item index="1">
                            <i class="el-icon-circle-check"></i>
                            <span slot="title">认证管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/detect">
                        <el-menu-item index="2">
                            <i class="el-icon-set-up"></i>
                            <span slot="title">检测管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/process">
                        <el-menu-item index="3">
                            <i class="el-icon-setting"></i>
                            <span slot="title">加工管理</span>
                        </el-menu-item>
                    </router-link>
                    <router-link to="/trace/delivery">
                        <el-menu-item index="4">
                            <i class="el-icon-truck"></i>
                            <span slot="title">配送管理</span>
                        </el-menu-item>
                    </router-link>
                </el-menu>
            </div>
        </el-aside>
        <el-main class="m-l-220 m-t-20 bg-white">
            <div class="flex flex-align-center flex-pack-justify">
                <div class="flex flex-align-center">
                    <el-input class="m-r-15" size="small" style="width:200px;" placeholder="搜索产品名称" suffix-icon="el-icon-search" v-model="vm.searchKey"></el-input>
                    <el-button type="primary" size="small" @click="search">搜索</el-button>
                </div>
                <el-button type="primary" size="small" @click="add">添加加工记录</el-button>
            </div>
            <el-row class="m-t-30">
                <el-col :span="24">
                    <el-table :height="tableHeight" :data="dataList" size="small" border stripe style="width: 100%">
                        <el-table-column prop="product" label="产品名称"></el-table-column>
                        <el-table-column prop="batch" label="产品批次"></el-table-column>
                        <el-table-column prop="processCon" label="加工内容" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="processUser" label="操作员"> </el-table-column>
                        <el-table-column prop="processTime" label="加工时间"></el-table-column>
                        <el-table-column fixed="right" label="操作" width="140">
                            <template slot-scope="scope">
                                <el-button type="primary" size="mini" icon="el-icon-search" @click="edit(scope.row)"></el-button>
                                <el-button type="danger" size="mini" icon="el-icon-delete" @click="remove(scope.row)"></el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-col>
            </el-row>
            <el-row class="m-t-30">
                <el-col>
                    <el-pagination 
                        class="text-right" 
                        background 
                        layout="total, prev, pager, next" 
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="vm.currentPage"
                        :page-size="vm.pageSize"
                        :total="vm.pageTotal">
                    </el-pagination>
                </el-col>
            </el-row>
            <el-dialog title="加工记录" :visible.sync="dialogVisible" width="800px" :close-on-click-modal="false">
                <el-form ref="form" size="small" :rules="rules" :model="form" label-width="150px">
                    <el-form-item label="所属地块" prop="plot">
                        <el-input style="width:260px;" v-model="form.plot" disabled></el-input>
                    </el-form-item>
                    <el-form-item label="产品名称" prop="product">
                        <el-select style="width:260px;" v-model="form.product" filterable placeholder="请选择产品">
                            <el-option
                                v-for="item in productList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="产品批次" prop="batch">
                        <el-select style="width:260px;" v-model="form.batch" filterable placeholder="请选择产品批次">
                            <el-option
                                v-for="item in batchList"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                            </el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="加工时间" prop="processTime">
                        <el-date-picker style="width:260px;" type="date" placeholder="选择日期" v-model="form.processTime"></el-date-picker>
                    </el-form-item>
                    <el-form-item label="操作员" prop="processUser">
                        <el-input style="width:260px;" v-model="form.processUser"></el-input>
                    </el-form-item>
                    <el-form-item label="加工内容" prop="processCon">
                        <el-input type="textarea" v-model="form.processCon"></el-input>
                    </el-form-item>
                </el-form>
                <span slot="footer" class="dialog-footer">
                    <el-button size="small" @click="dialogVisible = false">取消</el-button>
                    <el-button size="small" type="primary" @click="save">保存</el-button>
                </span>
            </el-dialog>
        </el-main>
    </div>
</template>

<script>
export default {
    data() {
        return {
            vm: {
                searchKey: '',
                currentPage: 1,
                pageSize: 10,
                pageTotal: 100,
            },
            dataList: [{
                product: '阳光玫瑰（东兰）',
                batch: '2019041010800001',
                processCon: '按等级包装，贴上唯一标识的二维码',
                processUser: '黄芳',
                processTime: '2018-06-14',
            }, {
                product: '阳光玫瑰（东兰）',
                batch: '2019041010800001',
                processCon: '按等级包装，贴上唯一标识的二维码',
                processUser: '黄芳',
                processTime: '2018-06-14',
            }, {
                product: '阳光玫瑰（东兰）',
                batch: '2019041010800001',
                processCon: '按等级包装，贴上唯一标识的二维码',
                processUser: '黄芳',
                processTime: '2018-06-14',
            }],
            dialogVisible: false,
            form:{
                plot: '东里旺旺葡萄园',
                product: '',
                batch: '',
                processTime: '',
                processUser: '',
                processCon: ''
            },
            productList:[],
            batchList:[],
            rules: {
                product: [
                    { required: true, message: '请选择产品', trigger: 'change' },
                ],
                batch: [
                    { required: true, message: '请选择产品批次', trigger: 'change' }
                ],
                processTime: [
                    { required: true, message: '请选择日期', trigger: 'change' }
                ],
                processCon: [
                    { required: true, message: '请填写加工内容', trigger: 'blur' }
                ]
            }
        }
    },
    computed:{
        tableHeight() {
            return document.documentElement.clientHeight - 275;
        }
    },
    methods:{
        search(){

        },
        add(){
            this.dialogVisible = true
        },
        edit(row) {
            this.resetForm()
            this.dialogVisible = true;
            this.form = row
        },
        save(){
            this.$refs['form'].validate((valid) => {
                if (valid) {
                    console.log(this.form)
                    this.dialogVisible = false
                } else {
                    this.$message.error("请检查您的输入！");
                    return false;
                }
            });
        },
        remove(row){
            this.$confirm('是否删除？', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
            }).catch(() => {
                          
            });
        },
        resetForm() {
            this.form = {
                plot: '东里旺旺葡萄园',
                product: '',
                batch: '',
                processTime: '',
                processUser: '',
                processCon: ''
            }
        },
        handleSizeChange(val) {
            console.log(val);
        },
        handleCurrentChange(val) {
            console.log(val);
        }
    }
}
</script>

<style>

</style>


